<!-- 系统管理 -->
<template>
	<div class="container" >
		<!-- <el-aside v-show="asideHide" style="z-index: 30;" class="aside" width="20px">
			<div @click="menuShowTransiton" class="delta-right"><span></span></div>
		</el-aside>
		<el-aside :class="{animateEnter: Dist, 'animate': !Dist }" class="aside" width="200px">
			<div @click="menuHideTransiton" v-show="!asideHide"  class="delta-left"><span></span></div>
			<ul class="menu">
				<li @click="linkClick(index)" :class="{active:curIndex == index}" v-for="(v,index) in menuList">
					<span>{{ v.text }}</span>
				</li>
			</ul>
			<el-tree :data="menuList" accordion @node-click="handleNodeClick"></el-tree>
		</el-aside> -->
		<div class="left-aside" @mouseleave="menuLeave" @mouseenter="menuEnter">
			<el-menu :default-active="activeIndex" router text-color="#fff" background-color="#333744" class="el-menu-vertical-demo" @select="handSelect" @open="handleOpen" @close="handleClose" :collapse="isCollapse">
				<el-submenu v-if="v.children && v.children.length > 0" v-for="(v,index) of menuList" :key="index" :index="v.path">
					<template slot="title">
						<i v-html="v.icon">{{ v.icon }}</i>
						<span slot="title">{{ v.text }}</span>
					</template>
					<el-menu-item-group>
						<el-menu-item v-for="(item,key) of v.children" :key="key" :index="item.path">{{ item.text }}</el-menu-item>
					</el-menu-item-group>
				</el-submenu>
				<el-menu-item v-if="!v.children || v.children.length == 0" v-for="(v,index) of menuList" :key="index" :index="v.path">
					<i v-html="v.icon">{{ v.icon }}</i>
					<span slot="title">{{ v.text }}</span>
				</el-menu-item>	
			</el-menu>
		</div>
		<keep-alive>
			<router-view></router-view>
		</keep-alive>
	</div>

</template>

<script>
import store from '@/store'

export default {
	name: 'SystemM',
	data() {
		return {
			childCurIndex: '0',
			curIndex:'0',
			asideHide: false,
			Dist: true,
			isCollapse: false,
			activeIndex: '/home/sysetsmM/menuManage',
			menuList: [
			{
				text: '菜单管理',
				path: '/home/sysetsmM/menuManage',
				icon: '<i style="font-size: 16px;" class="icon iconfont">&#xe604;</i>',
				children: [
				{
					text: '菜单管理',
					path: '/home/sysetsmM/menuManage'
				},
				{
					text: '操作按钮管理',
					path: '/home/sysetsmM/buttonManage'
				},
				{
					text: '模块操作管理',
					path: '/home/sysetsmM/modelManage'
				}
				]
			},
			{
				text: '角色管理',
				icon: '<i style="font-size: 16px;" class="icon iconfont">&#xe604;</i>',
				path: '/home/sysetsmM/roleManage'
			},
			{
				text: '组织管理',
				icon: '<i style="font-size: 16px;" class="icon iconfont">&#xe604;</i>',
				path: '/home/sysetsmM/divisionManage'
			},
			{
				text: '用户管理',
				icon: '<i style="font-size: 16px;" class="icon iconfont">&#xe604;</i>',
				path: '/home/sysetsmM/userManage'
			}
			]
		}
	},
	created() {
		this.getPath();
	},
	mounted() {

	},
	methods: {
		changeCurActive(index,key) {
			
			this.childCurIndex = key;
			console.log(index,key)
		},
		menuShowTransiton(){
			this.Dist = true;
			this.asideHide = false;
		},
		menuHideTransiton(){
			this.Dist = false;
			setTimeout(()=>{
				this.asideHide = true;
			},900)
		},
		linkClick(index){
			this.curIndex = index;
			switch  (index){
				case 1:
				this.$router.push('/home/sysetsmM/roleManage');
				break;
				case 2:
				this.$router.push('/home/sysetsmM/divisionManage');
				break;
				case 3:
				this.$router.push('/home/sysetsmM/userManage');
				break;
				default:
			}
		},
		//监听路由
		getPath(to) {
			var routeName = window.location.hash;
			this.activeIndex = routeName.replace('#','');
		},
		handleOpen(key, keyPath) {
			console.log(key, keyPath);
		},
		handleClose(key, keyPath) {
			console.log(key, keyPath);
		},
		menuLeave(){
			// console.log('移出')
			// this.isCollapse = true;
		},
		menuEnter(){
			// console.log('移入')
			// this.isCollapse = false;
		},
		handSelect(indexPath){
			console.log(indexPath)
			this.activeIndex = indexPath;
		}
	},
	store,
	watch:{
		'$route': 'getPath'
	}
}
</script>

<style scoped>
.container{flex: 1;padding:5px 0 0;background:#e7e7eb;border-left:#e7e7eb 4px solid;display:flex;overflow:hidden;border-right:#e7e7eb 4px solid;}
.aside{position: relative;background: #fff;height: 100%;padding: 10px 0 0;}
.animate{transition: margin-left 1s;margin-left: -200px !important;}
.animateEnter{transition: margin-left 1s;margin-left: 0px !important;}
.menu{z-index:20;width: 176px;border: 1px solid #ebeef5;}
.menu li span{height: 40px;line-height: 40px;padding-left: 20px;color: #666;cursor: pointer;display: block;}
.menu .active span,.menu li span:hover{color: #fff;background: #00a8d4;}
dl{padding: 0 20px;color: #666;font-size: 12px;}
dl dt{line-height: 32px;cursor: pointer;}
dl dd{padding-left: 43px;height: 40px;line-height: 40px;cursor: pointer;}
dl .active,dl dd:hover{background: #012442;color: #f19100;}
.delta-left,.delta-right{position: absolute;top: 20px;right: 5px; z-index:10;width: 10px;height: 20px;overflow: hidden;cursor: pointer;}
.delta-left span,.delta-right span{width: 10px;border: 10px solid #fff;border-right: 10px solid #d6dbdf;position: absolute; right: 0;top: 0;display: inline-block;}
.delta-right{width: 20px;}
.delta-right span{left: 10px;border: 10px solid #fff;border-left: 10px solid #d6dbdf;}
.el-menu-vertical-demo:not(.el-menu--collapse) {width: 200px;min-height: 400px;}
.el-menu-vertical-demo{border: none;}
.left-aside{background: #333744;}
</style>


